body {
/*  border: 1px solid black;*/
  margin: 1%;
  font-family: sans-serif;
}

.leaflet-container {
  border: 4px solid black;
  height: 650px;
  width: 100%;

}

.popups{
  font-size: 20px;
}

svg {
/*  border: 2px solid crimson;*/
  margin: auto;
}

.Hoover:hover {
  stroke: black;
  stroke-width: 2;
  opacity: 1.0;
  fill: red;
}

circle:hover {
  stroke: black;
  stroke-width: 2;
  opacity: 1.0
}

h1 {
/*  border: 1px solid limeGreen;*/
  text-align: center;
  text-transform: uppercase;
  color: limeGreen;
}

h2 {
  text-align: center;
  text-transform: lowercase;
}

select {
  display: block;
  margin: auto;
  font-size: 25px;
}


